// TOP ----------------------
.top_container {
    flex: 1.7;
    position: relative;
    overflow: hidden;

    .below_title {
        display:flex;
        flex-direction: row;
        height:100%;

        // @media screen and (max-width: 852px){
        //     flex:1;
        // }

        .top_left{
            flex:1;
            // background-color: pink;
            display: flex;
            justify-content: center;
            flex-direction: column;
            position: relative;

            .hide_about_container {
                opacity:0;
                height:0;
                // position: absolute;
                // width:700px;
                left:-800px;
            }
        
            .show_about_container {
                opacity:1;
                transition:left 1s;
                padding: 0 40px 0 40px;
                position: absolute;
                left:0;

                @media screen and (max-width: 852px){
                    padding:0 10px 0 10px;
                }
        
                .hi {
                    font-family: $noto;
                    font-size: 60px;
                    font-weight: 900;

                    @media screen and (max-width: 1650px){
                        font-size: 3.6em;
                    }
                }
        
                .me_desc {
                    font-family: $hind;
                    font-size:22px;
                    margin-bottom: 10px;

                    @media screen and (max-width: 1650px){
                        font-size: 1.4em;
                    }
                } 

                .resume{
                    text-decoration: none;
                    border: 1px solid black;
                    border-radius: 10%;
                    padding:5px;
                    color:white;
                    background-color: #3a618d;
                }
            }

            .block{
                position:relative !important;
            }
        }
    
        .top_right{
            flex:1;
            position:relative;
            // background-color: lightskyblue;
        }
    }

    

    .myphoto {
        position: absolute;
        right:0;
        width:500px;
        object-fit:cover;
        height:100%;
        transform: rotate(-90deg);
        top:0;
        
    }
    
    .hide_big_work_img {
        width:700px;
        height:100%;
        position: absolute;
        right:-100%;    
        opacity:0;   
    }

    .show_big_work_img {
        width:100%;
        height:100%;
        right:0;
        position: absolute;
        object-fit: contain;
        object-position: center;
        opacity:1;
        transition:right 1s, opacity 2s;
    }

    .hide_about_img {
        width:700px;
        height:100%;
        position: absolute;
        top:-100%;    
        opacity:0;   
    }

    .show_about_img {
        width:100%;
        height:100%;
        top:0;
        position: absolute;
        object-fit: contain;
        object-position: center;
        opacity:1;
        transition:top 1s;
    }

    .img_cut {
        -webkit-clip-path: polygon(43% 0, 100% 0, 100% 100%, 0% 100%);
        clip-path:polygon(43% 0, 100% 0, 100% 100%, 0% 100%);
    }

    .show_project_desc {
        opacity: 1;
        transition:opacity 1.5s, width 0.1s;
        transition-delay: 0.2s;
        font-family: $hind;
        padding: 0 40px 0 40px;

        @media screen and (max-width: 852px){
            padding:0 10px 0 10px;
        }

        .p_name {
            font-family: $noto;
            font-size:40px;
            font-weight: bold;

            @media screen and (max-width: 1365px){
                font-size: 2.9em;
            }
        }

        .date {
            font-size:20px;
            letter-spacing:1px;
            font-family: $hind;

            @media screen and (max-width: 1365px){
                font-size: 1.5em;
            }
        }

        .position {
            font-family: $noto;
            font-size: 30px;
            margin-bottom: 4px;
            @media screen and (max-width: 1365px){
                font-size: 2.2em;
            }
        }

        .tools, .p_desc, .role {
            font-size:18px;
            padding-bottom:5px;
            span {
                font-weight: bold;
            }

            @media screen and (max-width: 1365px){
                font-size: 1.3em;
            }
        }

    }

    .hide_project_desc {
        height:0;
        opacity:0;
        width:0px;
    }

}


//BOTTOM --------------
.bottom_container {
    flex:1;
    background-color:#77abe6;
    // background-color:#505050;
    padding:20px 40px 30px 50px;
    flex-direction: column;
    display:flex;

    @media screen and (max-width: 1024px){
        flex:0.8;
        padding: 20px 0px 20px 20px;
    }

    @media screen and (max-width: 688px){
        flex:0.6;
        padding: 10px 10px 10px 10px;
    }

    .tabs {
        margin-bottom:20px;
        display:flex;
        font-family: $noto;

        @media screen and (max-width: 688px){
            margin-bottom:20px;
        }

        b {
            font-size: 25px;

            @media screen and (max-width: 688px){
                font-size: 2.5em;
            }
        }

        span {
            font-size: 16px;
            cursor: pointer;
        }
    }

    .small_works_container {
        flex:7;
        flex-direction:row;
        display:flex;
        justify-content: space-between;
        padding:0 30px 0 30px;

        @media screen and (max-width: 1546px){
            padding:0;
        }

        @media screen and (max-width: 1024px){
            display:block;
            overflow:hidden;
            overflow-y:scroll;
            padding:0 48px 0 48px;
            text-align: center;
            zoom: 1;
        }

        @media screen and (max-width: 852px){
            display: inline-block;
            text-align: center;
            padding:0;
        }
    }

}

//FOOTER
.footer{
    flex:0.2;
    justify-content: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    padding-top:10px;
    padding-bottom:10px;
    font-family:$hind;
    background-color: #3a618d;
    color:white;

    @media screen and (max-width: 688px){
        flex:0.15;
    }

    .icon_container{
        margin-bottom: 5px;

        .middleIcon{
            margin-left: 20px;
            margin-right: 20px;
        }
    }

    
}

.social-icon{
    width:30px !important;
    height:30px !important;
}