.small_work {
    width:300px;
    height:100%;
    position: relative;
    margin-right:30px;

    @media screen and (max-width: 1024px){
        display:inline-block;
        width:180px;
        margin: 0;
    }

    @media screen and (max-width: 852px){
        width:180px;
        margin: 0;
    }

    @media screen and (max-width: 688px){
        width:26vw;
    }
    

    .small_work_img {
        width:100%;
        height:100%;
        position: absolute;
        border-radius: 5px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        transition-duration: 0.5s;
        bottom:0;
        left:0;
        box-shadow: 5px 9px 4px 0.1px rgba(0,0,0,0.07);
        -webkit-box-shadow: 5px 9px 4px 0.1px rgba(0,0,0,0.07);
        -moz-box-shadow: 5px 9px 9px 0.1px rgba(0,0,0,0.07);

        .viewmore {
            opacity:0;
        }
    
    }

    .small_work_img:hover {
        width:130%;
        height:130%;
        bottom:0;
        left:0;
        transition-duration: 0.7s;
        border-radius: 5px;
        z-index: 10;
        
    
        .viewmore {
            opacity:1;
            width:100%;
            height:100%;
            display:flex;
            justify-content:center;
            align-items:center;
            color:white;
            transition-duration: 0.5s;
            background-color: rgba(0,0,0,0.6);
            border-radius: 5px;
            
    
            a {
                padding:10px;
                border-radius:5px;
                border:1px solid white;
                font-size: 24px;
                text-decoration: none;
            }

            a:visited {
                text-decoration: none;
                color:white
            }

        }
    
    }

}

.small_work:hover {
    margin-right:80px;
    transition:margin 0.5s;
}

.small_work:nth-child(2), .small_work:nth-child(5){

    @media screen and (max-width: 1024px){
        margin-right:90px;
        margin-left:90px;
    }

    @media screen and (max-width: 852px){
        margin-right:56px; 
        margin-left:56px;
    }

    @media screen and (max-width: 688px){
        margin-right:30px; 
        margin-left:30px;
    }
    
}



.small_work:nth-child(1), .small_work:nth-child(2), .small_work:nth-child(3){

    @media screen and (max-width: 1024px){
        margin-bottom: 50px;
    }

    @media screen and (max-width: 852px){
        margin-bottom: 20px;
    }
   
    @media screen and (max-width: 688px){
        margin-bottom: 10px;
    }
}

